@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap');

.App {
  background: linear-gradient(135deg, #007fff 0%, #000000 100%);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

.message-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 100%;
  text-align: center;
}

.icon-container {
  margin-bottom: 20px;
}

.funny-message {
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 300;
  font-size: 1.75rem;
  line-height: 1.6;
  margin: 0;
  text-align: center;
  opacity: 0;
  animation: fadeIn 1000ms ease-in-out 2s forwards;
}

.signature {
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 300;
  font-size: 1.2rem;
  margin: 0;
  padding-top: 20px;
  opacity: 0;
  text-align: right;
  animation: fadeIn 1000ms ease-in-out 3.5s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .funny-message {
    font-size: 1.5rem;
    padding: 0 20px;
  }
}

@media (max-width: 480px) {
  .funny-message {
    font-size: 1.2rem;
    padding: 0 15px;
  }
}
